
















import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import {Equipment, EquipmentWithAug, EquipSet} from "@/@types/equip-set";
import xiUtils from "@/mixins/xiutils";

@Component
export default class EquipSetPerformanceView extends Mixins(xiUtils) {
  headers = [
    {
      text: "",
      align: "start",
      value: "NameWithUnit",
      width: 200,
    },
    { text: "", value: "TotalValue", width: 100 },
    { text: "", value: "WithoutSubValue", width: 100 },
  ];

  calculatedInfo: CalculatedInfo[] = [];

  @Prop({ default: null }) readonly equipSetInfo!: EquipSet | null;
  @Prop({ default: false }) readonly dirty!: boolean;

  @Watch("dirty")
  @Watch("equipSetInfo", { immediate: true, deep: true })
  equipSetInfoChanged() {
    this.calculatedInfo = [];
    if (this.equipSetInfo == null) {
      return;
    }

    for (let slotName in this.equipSetInfo) {
      if (Object.prototype.hasOwnProperty.call(this.equipSetInfo, slotName)) {
        let slot: EquipmentWithAug | null = this.equipSetInfo[slotName];
        if (slot == null) {
          continue;
        }
        let props = this.getPropertiesArray(slot.Equip);
        for (let prop of props) {
          let added = false;
          let nameWithUnit = prop.valueUnit
            ? prop.name + "(" + prop.valueUnit + ")"
            : prop.name;
          if (
            prop.name == "DMG" ||
            prop.name == "" ||
            prop.name == "" ||
            prop.name == "Delay"
          ) {
            switch (slotName) {
              case "Main":
                nameWithUnit += "()";
                break;
              case "Sub":
                nameWithUnit += "()";
                break;
              case "Range":
                nameWithUnit += "()";
                break;
              case "Ammo":
                nameWithUnit += "()";
                break;
            }
          }
          for (let info of this.calculatedInfo) {
            if (
              info.NameWithUnit == nameWithUnit &&
              info.HasValue == prop.hasValue
            ) {
              if (prop.hasValue && prop.value !== undefined) {
                if (info.WithoutSubValue !== undefined && slotName !== "Sub") {
                  info.WithoutSubValue += prop.value;
                }
                if (info.TotalValue !== undefined) {
                  info.TotalValue += prop.value;
                }
              }
              added = true;
              break;
            }
          }
          if (!added) {
            this.calculatedInfo.push({
              NameWithUnit: nameWithUnit,
              HasValue: prop.hasValue,
              TotalValue: prop.hasValue ? prop.value : undefined,
              WithoutSubValue:
                prop.hasValue && slotName !== "Sub" ? prop.value : undefined,
            });
          }
        }
      }
    }

    this.calculatedInfo.sort(function (a: CalculatedInfo, b: CalculatedInfo) {
      let oA =
        PropsStringOrder[a.NameWithUnit as keyof typeof PropsStringOrder] ??
        999;
      let oB =
        PropsStringOrder[b.NameWithUnit as keyof typeof PropsStringOrder] ??
        999;
      return oA - oB;
    });
  }
}

interface CalculatedInfo {
  NameWithUnit: string;
  HasValue: boolean;
  TotalValue?: number;
  WithoutSubValue?: number;
}

enum PropsStringOrder {
  "D" = 0,
  "STR",
  "DEX",
  "INT",
  "AGI",
  "MND",
  "VIT",
}
